import * as React from 'react';
import { List, Card } from 'antd';
import './index.scss';
import allMembers from '../../../utils/member_data';

const { Meta } = Card;
const members = allMembers;

export default class Member extends React.Component<{}, {}>{
    constructor(props: {}) {
        super(props);
    };

    public render() {
        return (
            <List
                grid={{
                    gutter: 16, xs: 1, sm: 1, md: 2, lg: 3, xl: 4, xxl: 6,
                }}
                dataSource={members}
                renderItem={(item: any) => (
                    <List.Item>
                        <Card
                            className="card-img"
                            hoverable
                            cover={<img className={
                                item.dead ? 'desaturate' : ''
                            } src={require(`../../../images/member_imgs/${item.src}.jpg`)} />}
                        >
                            <Meta className={item.dead ? 'desaturate' : ''} title={item.name} />
                        </Card>
                    </List.Item>
                )}
            />
        )
    };
}


